import React from 'react';
import QRCode from 'qrcode.react';

import {setLocalStorageVar,} from '../actions/utils';
import {encryptkey,} from '../actions/seedCrypt';
import translate from '../translate/translate';
import {config,} from '../actions/dev';
import passphraseGenerator from './../lib/agama-wallet-lib/build/crypto/passphrasegenerator';
import {ToastContainer, ToastStore} from './../lib/react-toast';

class CreateSeed extends React.Component {
    constructor() {
        super();
        this.state = {
            createPin: true,
            pinOverride: config.preload ? config.preload.pin : null,
            pinOverrideTooShort: false,
            pin: config.preload ? config.preload.pin : '',
            pinConfirm: false,
            confirmSeedSaved: false,
            seed: passphraseGenerator.generatePassPhrase(256),
            pristine: true,
            showQrCode: false,
            isPastedTheSamePhrase: false
        };
        this.defaultState = JSON.parse(JSON.stringify(this.state));
        this.updateInput = this.updateInput.bind(this);
        this.login = this.login.bind(this);
        this.toggleCreatePin = this.toggleCreatePin.bind(this);
        this.toggleConfirmSeed = this.toggleConfirmSeed.bind(this);
        this.generateKey = this.generateKey.bind(this);
        this.copyKey = this.copyKey.bind(this);
        this.reactToPasteAction = this.reactToPasteAction.bind(this);
    }

    componentWillMount() {
        this.setState({
            seed: passphraseGenerator.generatePassPhrase(256),
        });
    }

    componentWillReceiveProps(props) {
        // console.warn(props);
    }

    updateInput(e) {
        this.setState({
            [e.target.name]: e.target.value,
            pinOverrideTooShort: false,
        });
    }

    login() {
        if (!this.state.confirmSeedSaved &&
            this.state.pristine) {
            this.setState({
                pristine: false,
            });
        } else {
            if (this.state.createPin) {
                if (this.state.pinOverride &&
                    this.state.pinOverride.length >= 6) {
                    const _encryptedKey = encryptkey(this.state.pinOverride, this.state.seed);

                    setLocalStorageVar('seed', {encryptedKey: _encryptedKey});
                    
                    // this should rewritten when we'll have time
                    window.__seed = this.state.seed;

                    this.props.login(this.state.seed);
                    this.setState(this.defaultState);
                } else {
                    this.setState({
                        pinOverrideTooShort: true,
                        wrongPin: false,
                    });
                }
            } else {
                this.props.login(this.state.seed);
                this.setState(this.defaultState);
            }
        }
    }

    toggleCreatePin() {
        this.setState({
            createPin: !this.state.createPin,
        });
    }

    toggleConfirmSeed() {
        this.setState({
            confirmSeedSaved: !this.state.confirmSeedSaved,
        });
    }

    generateKey() {
        this.setState({
            seed: passphraseGenerator.generatePassPhrase(256),
        });
    }

    copyKey() {
        var copyText = document.getElementById("seed");
        copyText.select();
        document.execCommand("copy");
        window.getSelection().removeAllRanges();
        
        ToastStore.info("Copied to clipboard", 2000, "toast-message");
    }

    // the idea is to disable the 'Generate' button when pasting an existing passphrase.
    // so, we need to react to pasting from the clipboard and setting isPastedTheSamePhrase to true and disable the button
    reactToPasteAction(event) {
        if (event.type === 'paste' && event.target.name === 'seed') {
            this.setState({isPastedTheSamePhrase: true});
        }
    }

    componentDidMount() {
        document.addEventListener("paste", this.reactToPasteAction);
    }

    componentWillUnmount() {
        document.removeEventListener("paste", this.reactToPasteAction);
    }

    render() {
        console.log('Create Seed');
        if (this.props.activeSection === 'create-seed') {
            let isSeedExist = this.state.seed && this.state.seed.length > 0;
            let isPastedTheSamePhrase = this.state.isPastedTheSamePhrase;
            return (
                <div className="form create-seed">
                    {/* <h4 className="seed-header">{translate('LOGIN.CREATE_RECOVER_WALLET')}</h4> */}
                    <p className="seed-label">Generate new passphrase</p>
                    <div className="textarea-wrap">
                        <textarea className="edit-field" id="seed" name="seed" style={{"height": "80px"}}
                                  onChange={this.updateInput}
                                  value={this.state.seed}>
                        </textarea>
                        <button className="button-copy" onClick={this.copyKey}>
                        </button>
                    </div>
                    <div className='group3'>
                        <div className="btn-inner"
                             onClick={this.generateKey}>
                            <div
                                className={`btn ${isSeedExist && !isPastedTheSamePhrase ? '' : 'disabled-btn'}`}>{translate('LOGIN.GENERATE')}</div>
                        </div>
                    </div>
                    <div className="text-center margin-top-25">
                            <QRCode
                                value={this.state.seed}
                                size={230}/>
                    </div>
                    <div className="margin-bottom-25 margin-top-25">
                        <label className="switch hide">
                            <input
                                type="checkbox"
                                value="on"
                                checked={this.state.createPin}/>
                            <div
                                className="slider"
                                onClick={this.toggleCreatePin}></div>
                        </label>
                        <div
                            className="toggle-label pointer hide"
                            onClick={this.toggleCreatePin}>
                            {translate('LOGIN.OVERRIDE_PIN')}
                        </div>
                        <p className="seed-label">Introduce a new PIN to access to your wallet</p>
                        {this.state.createPin &&
                        <div className="edit edit-field">
                            <input
                                type="password"
                                className="form-password"
                                name="pinOverride"
                                onChange={this.updateInput}
                                placeholder={translate('LOGIN.ENTER_6_DIGIT_PIN')}
                                value={this.state.pinOverride || ''}/>
                        </div>
                        }
                        {this.state.createPin &&
                        this.state.pinOverrideTooShort &&
                        <div className="error margin-top-15 sz350">
                            <i className="fa fa-warning"></i> {translate('LOGIN.PIN_TOO_SHORT')}
                        </div>
                        }
                    </div>
                    <div className="margin-top-30 margin-bottom-25 sz350">
                        <label className="switch">
                            <input
                                type="checkbox"
                                value="on"
                                checked={this.state.confirmSeedSaved}/>
                            <div
                                className="slider"
                                onClick={this.toggleConfirmSeed}></div>
                        </label>
                        <div
                            className={`toggle-label pointer ${this.state.confirmSeedSaved ? 'active' : ''}`}
                            onClick={this.toggleConfirmSeed}>
                            {translate('LOGIN.I_CONFIRM_I_SAVED_SEED')}
                        </div>
                        {!this.state.confirmSeedSaved &&
                        !this.state.pristine &&
                        <div className="error margin-top-15">
                            <i className="fa fa-warning"></i> {translate('LOGIN.CONFIRMATION_REQUIRED')}
                        </div>
                        }
                    </div>
                    <div className="group3">
                        <div className="btn-inner" onClick={this.login}>
                            <div className="btn">{translate('LOGIN.DONE')}</div>
                        </div>
                    </div>

                    <ToastContainer store={ToastStore} position={ToastContainer.POSITION.BOTTOM_CENTER} className="toast-message" />
                </div>
            );
        } else {
            return null;
        }
    }
}

export default CreateSeed;