import React, {Component} from 'react';
import {getLocalStorageVar} from "../actions/utils";
import translate from "../translate/translate";

class SplashScreen extends Component {
    constructor(props) {
        super(props)
    }

    renderLoginButton = (goToLogin) => {
        // check if we have the existing seed value for login
        const _encryptedKey = getLocalStorageVar('seed');
        if (_encryptedKey) {
            return (
                <button className="button action" onClick={() => goToLogin()}>login with pin</button>
            );
        }
        return null;
    };

    render() {
        const {goToLogin, goToAddCoin, goToCreateSeedPage, goToCreateRecoverSeedPage} = this.props;
        let styles = {
            // backgroundColor: "#f68d1f",
            // height: "100%",
        };
        return (
            <div className="container" style={styles}>
                <img className="img_wrap" src={`/images/pungo_logo_splash.svg`} width={180} height={90}/>
                <div className="form-wrap">
                    {this.renderLoginButton(goToLogin)}
                    <button className="button action" onClick={() => goToCreateSeedPage()}>{translate('LOGIN.CREATE_WALLET').toUpperCase()}</button>
                    <button className="button action" onClick={() => goToCreateRecoverSeedPage()}>{translate('LOGIN.RECOVER_WALLET').toUpperCase()}</button>
                </div>
            </div>
        );
    }
}


export default SplashScreen;
