import React from 'react';
import {formatValue} from './../lib/agama-wallet-lib/build/utils';
import {secondsToString} from './../lib/agama-wallet-lib/build/time';
import {
    explorerList,
    isKomodoCoin,
} from './../lib/agama-wallet-lib/build/coin-helpers';
import translate from '../translate/translate';
import Spinner from './Spinner';
import QRCode from 'qrcode.react';
import {ToastContainer, ToastStore} from './../lib/react-toast';

class Transactions extends React.Component {
    constructor() {
        super();
        this.state = {
            toggledTxDetails: null,
            showQR: false,
        };
        this.toggleTxDetails = this.toggleTxDetails.bind(this);
        this.openExternalURL = this.openExternalURL.bind(this);
        this.isInterestDefined = this.isInterestDefined.bind(this);
        this.toggleQR = this.toggleQR.bind(this);
        this.toggleHome = this.toggleHome.bind(this);
        this.showClaimButton = this.showClaimButton.bind(this);

        this.copyKey = this.copyKey.bind(this);
    }

    copyKey() {
        var range = document.createRange();
        range.selectNode(document.getElementById("hash-value"));
        window.getSelection().removeAllRanges();
        window.getSelection().addRange(range);
        document.execCommand("copy");
        window.getSelection().removeAllRanges();

        ToastStore.info("Copied to clipboard", 2000, "toast-message");
    }

    toggleHome() {
        this.setState({
            showQR: false,
        });
        this.props.dashboardRefresh();

        ToastStore.info("Synchronized", 2000, "toast-message");
    }

    toggleQR() {
        this.setState({
            showQR: !this.state.showQR,
        });
    }

    showClaimButton() {
        if (this.props.coin === 'kmd' &&
            this.props.balance &&
            this.props.balance.interest &&
            this.props.balance.interest > 0) {
            return true;
        }
    }

    showSendButton() {
        if (this.props.balance &&
            this.props.balance.balance &&
            this.props.balance.balance > 0) {
            return true;
        }
    }

    isInterestDefined() {
        if (this.props.balance &&
            this.props.balance.interest &&
            this.props.balance.interest > 0) {
            return true;
        }
    }

    componentWillReceiveProps(props) {
        if (props.coin !== this.props.coin) {
            this.setState({
                toggledTxDetails: null,
            });
        }
    }

    toggleTxDetails(index) {
        this.setState({
            toggledTxDetails: index === this.state.toggledTxDetails ? null : index,
        });
    }

    openExternalURL(url) {
        window.open(url, '_system');
    }

    renderTxAmount(tx, amountOnly) {
        let _amountNegative;

        if ((tx.category === 'send' ||
            tx.category === 'sent') ||
            (tx.type === 'send' ||
                tx.type === 'sent')) {
            _amountNegative = -1;
        } else {
            _amountNegative = 1;
        }

        if (Number(tx.interest) === Number(tx.amount)) {
            _amountNegative = -1;
        }

        return (
            <span>
        {Number(tx.interest) === Number(tx.amount) &&
        <span>+</span>
        }
                {/*{formatValue(tx.amount) * _amountNegative || translate('TRANSACTIONS.UNKNOWN')} {Number(tx.amount) === 0 ? '' : this.props.coin.toUpperCase()}*/}
                {tx.amount * _amountNegative || translate('TRANSACTIONS.UNKNOWN')} {Number(tx.amount) === 0 ? '' : ''}
                {/* {formatValue(tx.amount) * _amountNegative || translate('TRANSACTIONS.UNKNOWN')} {Number(tx.amount) === 0 ? '' : ''} */}
                {tx.interest &&
                !amountOnly &&
                (Number(tx.interest) !== Number(tx.amount)) &&
                <div className="tx-interest">+{formatValue(Math.abs(tx.interest))}</div>
                }
      </span>
        );
    };

    renderSendReceiveBtn() {
        return (
            /*<div className={'send-receive-block' + (this.showClaimButton() ? ' three-btn' : '')}>*/
            <div className='send-receive-block'>
                <div className="send-receive-block-inner">
                    <button disabled={!this.showSendButton()} type="button" onClick={() => this.props.changeActiveSection('send')} className="btn send-btn">
                        <img className="" src="/images/template/transactions/send_icon.svg"/> {translate('DASHBOARD.SEND').toUpperCase()}
                    </button>
                    <button type="button" className={`btn receive-btn ${this.state.showQR ? 'active' : ''}`} onClick={this.toggleQR}>
                        <img className="" src="/images/template/transactions/receive_icon.svg"/> {translate('DASHBOARD.RECEIVE').toUpperCase()}
                    </button>
                    <button type="button" className="btn sync-btn" onClick={this.toggleHome}>
                        <img className="" src="/images/template/transactions/sync_icon.svg"/> {translate('DASHBOARD.SYNC').toUpperCase()}
                    </button>
                    {/*{this.state.showQR &&*/}
                    {/*<div className="receive-qr">*/}
                    {/*{this.props.address &&*/}
                    {/*<div>*/}
                    {/*<QRCode*/}
                    {/*value={this.props.address}*/}
                    {/*size={198}/>*/}
                    {/*/!*<div className="text-center">{this.props.address}</div>*!/*/}
                    {/*</div>*/}
                    {/*}*/}
                    {/*</div>*/}
                    {/*}*/}
                    {/*{this.showClaimButton() &&*/}
                    {/*<button*/}
                    {/*type="button"*/}
                    {/*className="btn btn-info waves-effect waves-light margin-left-20 btn-claim"*/}
                    {/*onClick={this.props.toggleKMDInterest}>*/}
                    {/*<i className="fa fa-dollar"></i> {translate('DASHBOARD.CLAIM')}*/}
                    {/*</button>*/}
                    {/*}*/}
                </div>
                {this.state.showQR &&
                <div className="qr-code-container">
                    <div className="receive-qr">
                        {this.props.address &&
                        <div>
                            <QRCode value={this.props.address} size={198}/>
                        </div>
                        }
                    </div>
                </div>
                }
            </div>
        );
    }

    render() {
        if (this.props.activeSection === 'dashboard') {
            const _transactions = this.props.transactions;
            let _items = [];

            console.log(this.props);

            if (_transactions) {
                for (let i = 0; i < _transactions.length; i++) {
                    _items.push(
                        <div
                            onClick={ () => this.openExternalURL(`${explorerList[this.props.coin.toUpperCase()]}/tx/${_transactions[i].txid}`) }
                            className={`item ${_transactions[i].interest && Math.abs(_transactions[i].interest) > 0 ? 'received' : _transactions[i].type}`} key={`transaction-${i}`}>
                            <div className="direction">
                                <div className="transaction-direction">
                                    <img className="transaction-arrow" src={`/images/template/transactions/${_transactions[i].type === 'received' ? 'in' : 'out'}_icon.svg`}/>
                                    {_transactions[i].type === 'received' ? translate('TRANSACTIONS.IN').toUpperCase() : translate('TRANSACTIONS.OUT').toUpperCase()}
                                </div>
                                {/*{_transactions[i].type}*/}
                            </div>
                            <div className="date">{secondsToString(_transactions[i].timestamp).toUpperCase()}</div>
                            {/*<div className="amount-fiat">$0</div> */}
                            <div className="amount-native">{this.renderTxAmount(_transactions[i])}</div>
                            <div className="right-arrow">
                                <img src="/images/template/transactions/rightarrow_icon.svg"/>
                            </div>
                        </div>
                    );

                    /*_items.push(
                      <div
                        className="txlist-transaction"
                        key={ `transaction-${i}` }>
                        <div>
                          { this.renderTxType(_transactions[i].type) }
                          <span className="margin-left-20">{ this.renderTxAmount(_transactions[i]) }</span>
                          <span className="margin-left-20">{ secondsToString(_transactions[i].timestamp) }</span>
                          <span
                            onClick={ () => this.toggleTxDetails(i) }
                            className={ 'details-toggle fa ' + (this.state.toggledTxDetails === i ? 'fa-caret-up' : 'fa-caret-down') }></span>
                        </div>
                        { this.state.toggledTxDetails !== i &&
                          <div className="margin-top-10 padding-bottom-10 txid-hash">
                          { _transactions[i].txid }
                          </div>
                        }
                        { this.state.toggledTxDetails === i &&
                          <div className="margin-top-10 padding-bottom-10 tx-details">
                            <div>{ translate('TRANSACTIONS.DIRECTION') }: { _transactions[i].type }</div>
                            <div>{ translate('TRANSACTIONS.AMOUNT') }: { this.renderTxAmount(_transactions[i], true) } { this.props.coin.toUpperCase() }</div>
                            { _transactions[i].interest &&
                              Math.abs(_transactions[i].interest) > 0 &&
                              <div>{ translate('TRANSACTIONS.INTEREST') }: { formatValue(Math.abs(_transactions[i].interest)) } KMD</div>
                            }
                            <div>{ translate('TRANSACTIONS.CONFIRMATIONS') }: { _transactions[i].confirmations }</div>
                            { this.props.coin === 'kmd' &&
                              <div>Locktime: { _transactions[i].locktime }</div>
                            }
                            <div>
                            { translate('TRANSACTIONS.TIME') }: { secondsToString(_transactions[i].timestamp) }
                              { isKomodoCoin(this.props.coin) &&
                                <button
                                  onClick={ () => this.openExternalURL(`${explorerList[this.props.coin.toUpperCase()]}/tx/${_transactions[i].txid}`) }
                                  className="margin-left-20 btn btn-sm white btn-dark waves-effect waves-light ext-link">
                                  <i className="fa fa-external-link"></i>Explorer
                                </button>
                              }
                            </div>
                            <div>
                            { translate('TRANSACTIONS.TX_HASH') } <div className="txid-hash">{ _transactions[i].txid }</div>
                            </div>
                          </div>
                        }
                      </div>
                    );*/
                }
            }

            return (
                <div className="transactions-ui">
                    <div className="individualportfolio">
                        <div className="coin-address-container">
                            <div className="section-label">{this.props.coin.toUpperCase()} {translate('TRANSACTIONS.COIN_ADDRESS')}</div>
                            <div className="coin-hash">
                                <div id="hash-value" className="hash-value">
                                    {this.props.address}
                                </div>
                                {/* <inpuy className="hash-value" value={this.props.address} /> */}
                                <div className="copy-hash">
                                    <button type="button" className="btn copy-icon" onClick={this.copyKey}>
                                        <img src={`images/template/transactions/copy_icon.svg`}/>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div className="individualportfolio-inner">
                            {/*<div className="cryptocardbtc-block">*/}
                            {/*<div className="cryptocardbtc">*/}
                            {/*<img*/}
                            {/*className="coin-icon"*/}
                            {/*src={ `/images/cryptologo/${this.props.coin}.png` } />*/}
                            {/*<div className="coin-title">{ translate('COINS.' + this.props.coin.toUpperCase()) }</div>*/}
                            {/*<div className="coin-balance">*/}
                            {/*<div className="balance">*/}
                            {/*{ translate('BALANCE.BALANCE') }: { this.props.balance ? formatValue(this.props.balance.balance) : 0 } { this.props.coin.toUpperCase() }*/}
                            {/*</div>*/}
                            {/*{ this.isInterestDefined() &&*/}
                                <div className="coin-balance-container">
                                <div className="section-label">{translate('BALANCE.BALANCE')}</div>
                                <div className="coin-balance-value">
                                <strong>{this.props.balance ? this.props.balance.balance : 0}</strong> {this.props.coin.toUpperCase()}
                                {/* <strong>{this.props.balance ? formatValue(this.props.balance.balance) : 0}</strong> {this.props.coin.toUpperCase()} */}
                                </div>
                                </div>
                            {/*}*/}
                            {/*</div>*/}
                            {/*{ !this.props.loading &&*/}
                            {/*this.props.auth &&*/}
                            {/*this.props.activeSection === 'dashboard' &&*/}
                            {/*(_items && _items.length > 0) &&*/}
                            {/*<i*/}
                            {/*onClick={ this.props.dashboardRefresh }*/}
                            {/*className="fa fa-refresh dashboard-refresh"></i>*/}
                            {/*}*/}
                            {/*{ this.props.loading &&*/}
                            {/*this.props.activeSection === 'dashboard' &&*/}
                            {/*<Spinner />*/}
                            {/*}*/}
                            {/*</div>*/}
                            {/*</div>*/}
                            {this.renderSendReceiveBtn()}
                            {!this.state.showQR &&
                            <div className="latest-transactions-container">
                                {this.props.loading &&
                                !this.props.transactions &&
                                !this.state.showQR &&
                                <div className="section-label">{translate('TRANSACTIONS.LOADING_HISTORY')}...</div>
                                }
                                {this.props.transactions &&
                                !this.state.showQR &&
                                <div className="section-label">{!_items.length ? translate('TRANSACTIONS.NO_HISTORY') : translate('TRANSACTIONS.LAST_TX')}</div>
                                }
                                {(_items && _items.length > 0 && !this.state.showQR) &&
                                <div className="transactions-list">
                                    {_items}
                                </div>
                                }
                            </div>
                            }
                        </div>
                    </div>
                    
                    <ToastContainer store={ToastStore} position={ToastContainer.POSITION.BOTTOM_CENTER} className="toast-message" />
                </div>
            );
        }
    }
}

export default Transactions;
