import React from 'react';
import jsQR from 'jsqr';
import {
  maskPubAddress,
  setLocalStorageVar,
  getLocalStorageVar,
  convertURIToImageData,
} from '../actions/utils';
import {
  encryptkey,
  decryptkey,
} from '../actions/seedCrypt';
import translate from '../translate/translate';
import {
  devlog,
  config,
} from '../actions/dev';

class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      passphrase: config.preload ? config.preload.seed : null,
      createPin: false,
      pinOverride: config.preload ? config.preload.pin : null,
      pinOverrideTooShort: false,
      pin: config.preload ? config.preload.pin : '',
      wrongPin: false,
      qrScanError: false,
    };
    this.defaultState = JSON.parse(JSON.stringify(this.state));
    this.updateInput = this.updateInput.bind(this);
    this.triggerKey = this.triggerKey.bind(this);
    this.login = this.login.bind(this);
    this.toggleCreatePin = this.toggleCreatePin.bind(this);
    this.scanQR = this.scanQR.bind(this);    
  }

  updateInput(e) {
    this.setState({
      [e.target.name]: e.target.value,
      wrongPin: false,
      qrScanError: false,
      pinOverrideTooShort: false,
    });
  }

  scanQR() {
    MeteorCamera.getPicture({
      quality: 100,
    }, (error, data) => {
      if (error) {
        this.setState({
          qrScanError: true,
        });
        setTimeout(() => {
          this.setState({
            qrScanError: false,
          });
        }, 5000);
      } else {
        convertURIToImageData(data)
        .then((imageData) => {
          const decodedQR = jsQR.decodeQRFromImage(
            imageData.data,
            imageData.width,
            imageData.height
          );

          if (!decodedQR) {
            this.setState({
              qrScanError: true,
            });

            setTimeout(() => {
              this.setState({
                qrScanError: false,
              });
            }, 5000);
          } else {
            this.setState({
              qrScanError: false,
              passphrase: decodedQR,
            });
          }
        });
      }
    });
  }

  login(isPinAccess) {
    if (isPinAccess) {
      // decrypt
      const _encryptedKey = getLocalStorageVar('seed');

      if (_encryptedKey &&
          _encryptedKey.encryptedKey &&
          this.state.pin &&
          this.state.pin.length >= 6) {
        const _decryptedKey = decryptkey(this.state.pin, _encryptedKey.encryptedKey);

        if (_decryptedKey) {
          this.props.loginAfterSelectCoin(_decryptedKey);
          this.setState(this.defaultState);
        } else {
          this.setState({
            pinOverrideTooShort: false,
            wrongPin: true,
          });
        }
      } else {
        this.setState({
          pinOverrideTooShort: false,
          wrongPin: true,
        });
      }
    } else {
      if (this.state.createPin) {
        if (this.state.pinOverride &&
            this.state.pinOverride.length >= 6) {
          const _encryptedKey = encryptkey(this.state.pinOverride, this.state.passphrase);

          setLocalStorageVar('seed', { encryptedKey: _encryptedKey });

          this.props.loginAfterSelectCoin(this.state.passphrase);
          // this.props.login(this.state.passphrase);
          this.setState(this.defaultState);
        } else {
          this.setState({
            pinOverrideTooShort: true,
            wrongPin: false,
          });
        }
      } else {
        this.props.loginAfterSelectCoin(this.state.passphrase);
        // this.props.login(this.state.passphrase);
        this.setState(this.defaultState);
      }
    }
  }

  toggleCreatePin() {
    this.setState({
      createPin: !this.state.createPin,
    });
  }

  triggerKey(key) {
    if (key === 'back') {
      this.setState({
        pin: this.state.pin.slice(0, -1),
        wrongPin: false,
      });
    } else if (key === 'remove') {
      this.setState({
        pin: '',
        wrongPin: false,
      });
    } else {
      this.setState({
        pin: this.state.pin + key,
        wrongPin: false,
      });
    }
  }

  renderKeyPad() {
    let _items = [];

    for (let i = 0; i < 10; i++) {
      _items.push(
        <button
          key={ `login-keypad-${i}` }
          className="btn btn-lg btn-primary"
          onClick={ () => this.triggerKey(i) }>
          <span className="ladda-label">
          { i }
          </span>
        </button>
      );
    }

    _items.push(
      <button
        key={ `login-keypad-back` }
        className="btn btn-lg btn-primary"
        onClick={ () => this.triggerKey('back') }>
        <span className="ladda-label padding-fix">
        <i className="fa fa-long-arrow-left"></i>
        </span>
      </button>
    );

    _items.push(
      <button
        key={ `login-keypad-remove` }
        className="btn btn-lg btn-primary"
        onClick={ () => this.triggerKey('remove') }>
        <span className="ladda-label padding-fix">
        <i className="fa fa-remove"></i>
        </span>
      </button>
    );

    return _items;
  }

  componentDidMount() {
    this.inputPin.focus(); 
  }

  render() {
    const {goToCreateSeedPage, goToCreateRecoverSeedPage} = this.props;
    let styles = {
      // backgroundColor: "#f68d1f",
      // height: "100%",
    };
    console.log('Login')
    // if ((this.props.activeSection === 'login' || (!this.props.auth && this.props.activeSection !== 'addcoin')) &&
    if (this.props.activeSection === 'login' &&
        // this.props.coins &&
        // Object.keys(this.props.coins).length &&
        // this.props.activeSection !== 'create-seed' &&
        // this.props.activeSection !== 'offlinesig' &&
        // this.props.activeSection !== 'pin' &&
        true
      ) {
      return (
          <div className="container" style={styles}>
              <img className="img_wrap" src={`/images/pungo_logo_splash.svg`} width={180} height={90}/>

              <div className="form-wrap">
                  <p className="login-label">login with pin</p>
                  <div className="input-wrap">
                      <input
                          ref={(input) => { this.inputPin = input; }}
                          type="password"
                          className="form-input"
                          name="pin"
                          onChange={this.updateInput}
                          placeholder={translate('LOGIN.ENTER_PIN')}
                          value={this.state.pin}/>
                      <img className="login-action" src={`/images/template/login/circle_right_arrow.svg`} onClick={ () => this.login(true) }
                           align="right"/>
                  </div>
                  <button className="button action" onClick={() => goToCreateSeedPage()}>{translate('LOGIN.CREATE_WALLET').toUpperCase()}</button>
                    <button className="button action" onClick={() => goToCreateRecoverSeedPage()}>{translate('LOGIN.RECOVER_WALLET').toUpperCase()}</button>
              </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Login;