import React from 'react';
import Spinner from './Spinner';

import {formatValue} from './../lib/agama-wallet-lib/build/utils';
import translate from '../translate/translate';

class Overview extends React.Component {
    constructor() {
        super();
        this.state = {};
    }

    renderOverview() {
        if (this.props.overview) {
            const _overview = this.props.overview;
            let _items = [];
            let _totalUSDBalance = 0;
            let _totalBTCBalance = 0;

            for (let i = 0; i < _overview.length; i++) {
                _totalUSDBalance += _overview[i].balanceUSD;
                _totalBTCBalance += _overview[i].balanceBTC;
            }

            for (let i = 0; i < _overview.length; i++) {
                _items.push(
                    <div key={`overview-coins-${_overview[i].coin}`} className="overview-coin" onClick={() => this.props.switchCoin(_overview[i].coin, true)}>
                        <div className="coin-icon">
                            <img src={`/images/cryptologo/${_overview[i].coin}_icon.svg`}/>
                        </div>
                        <div className="coin-details">
                            <div className="coin-balance">
                                {Number(_overview[i].balanceNative).toFixed(4)} <span> {_overview[i].coin.toUpperCase()}</span>
                                {/* {formatValue(_overview[i].balanceNative)} <span> {_overview[i].coin.toUpperCase()}</span> */}
                            </div>
                            <div className="coin-balance-usd">
                                {Number(_overview[i].balanceUSD).toFixed(2)} <span> EUR</span>
                                {/* {formatValue(_overview[i].balanceUSD)} <span> EUR</span> */}
                            </div>
                        </div>
                    </div>
                );
            }

            return (
                <div>
                    <div className="coins-container">
                        {_items}
                    </div>
                    <div className="total-coins">
                        <div className="yourcoins">{translate('OVERVIEW.YOUR_COINS')}</div>
                        <div className="coins-value"> ~ {Number(_totalUSDBalance).toFixed(2)}<span> EUR</span></div>
                        {/* <div className="coins-value"> ~ {formatValue(_totalUSDBalance)}<span> EUR</span></div> */}
                    </div>
                    <button key={`manage-coins`} className="manage-coins-btn" onClick={() => this.props.changeActiveSection('manage_coins')}>
                        <img src={`/images/template/home/managecoins_icon.svg`}/>
                        manage coins
                    </button>
                </div>
            );
        } else {
            return (
                <Spinner/>
            );
        }
    }

    render() {
        return (
            <div className="overview-ui">
                <div className="home">
                    {this.props.overview === 'error' &&
                    <div className="con-error">
                        <i className="fa fa-warning error"></i> <span className="error">{translate('OVERVIEW.PRICES_ERROR')}</span>
                    </div>
                    }
                    {this.props.overview !== 'error' &&
                    <div className="home-inner">
                        {this.renderOverview()}
                        {/*<div className="yourcoins">{translate('OVERVIEW.YOUR_COINS')}</div>*/}
                        {/*<img
                        className="combinedshape2"
                        src="/images/template/home/home-combined-shape 2.png" />*/}
                    </div>
                    }
                </div>
            </div>
        );
    }
}

export default Overview;
