import React from 'react';

import { getLocalStorageVar } from '../actions/utils';
import { decryptkey } from '../actions/seedCrypt';
import translate from '../translate/translate';
import QRCode from 'qrcode.react';
import {
  devlog,
  config,
} from '../actions/dev';
import {ToastContainer, ToastStore} from './../lib/react-toast';

class Recovery extends React.Component {
  constructor() {
    super();
    this.state = {
      passphrase: config.preload ? config.preload.seed : null,
      pin: config.preload ? config.preload.pin : null,
      wrongPin: false,
    };
    this.defaultState = JSON.parse(JSON.stringify(this.state));
    this.updateInput = this.updateInput.bind(this);
    this.decodeSeed = this.decodeSeed.bind(this);
    this.copyKey = this.copyKey.bind(this);
  }

  componentWillReceiveProps(props) {
    if (props.activeSection !== 'recovery' &&
        this.state.passphrase) {
      this.setState(this.defaultState);
    }
  }

  updateInput(e) {
    this.setState({
      [e.target.name]: e.target.value,
      wrongPin: false,
    });
  }

  decodeSeed() {
    const _encryptedKey = getLocalStorageVar('seed');
    const _decryptedKey = decryptkey(this.state.pin, _encryptedKey.encryptedKey);

    if (_decryptedKey) {
      this.setState({
        wrongPin: false,
        pin: null,
        passphrase: _decryptedKey,
      });
    } else {
      this.setState({
        wrongPin: true,
      });
    }
  }

  copyKey() {
    var range = document.createRange();
    range.selectNode(document.getElementById("seed"));
    window.getSelection().removeAllRanges();
    window.getSelection().addRange(range);
    document.execCommand("copy");
    window.getSelection().removeAllRanges();

    ToastStore.info("Copied to clipboard", 2000, "toast-message");
}

  render() {
    return (
      <div className="form recovery">
       {/* <div className="settings-header">recover passphrase</div> */}
        <div className="margin-top-45 text-center pin-label">
        { translate('RECOVERY.PROVIDE_YOUR_PIN') }
        </div>
        <div className="margin-bottom-25">
          <div className="edit">
            <input
              type="password"
              className="form-control"
              name="pin"
              onChange={ this.updateInput }
              placeholder={ translate('LOGIN.ENTER_PIN') }
              value={ this.state.pin || '' } />
          </div>
          { this.state.wrongPin &&
            <div className="error margin-top-15 sz350">
              <i className="fa fa-warning"></i> { translate('LOGIN.WRONG_PIN') }
            </div>
          }
        </div>
        <div
          disabled={ !this.state.pin }
          onClick={ this.decodeSeed }
          className="group3 margin-top-10">
          <div className="btn-inner">
            <div className="btn">{ translate('RECOVERY.SHOW') }</div>
          </div>
        </div>
        { this.state.passphrase &&
                   <div>
                   <div className="textarea-wrap">
                         <textarea className="edit-field" id="seed" name="seed" style={{"height": "80px"}}
                                   onChange={this.updateInput}
                                   value={this.state.passphrase}>
                         </textarea>
                         <button className="button-copy" onClick={this.copyKey}>
                         </button>
                   </div>     
                   <div className="text-center margin-top-30">
                                     <QRCode
                                         value={this.state.passphrase}
                                         size={230}/>
                   </div>
                   </div>
        }

        <ToastContainer store={ToastStore} position={ToastContainer.POSITION.BOTTOM_CENTER} className="toast-message" />
      </div>
    );
  }
}

export default Recovery;